* {
  margin: 0;
  padding: 0;
}

.container {
  outline: 1px solid red;
  width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
}

.js-list {
  flex-wrap: wrap;
  gap: 30px;
  list-style: none;
  display: flex;
}

.item {
  background-color: #dcdcdc;
  outline: 1px solid gray;
  flex-basis: calc(33.3333% - 20px);
}

.image {
  width: 100%;
  height: 300px;
  display: block;
}

.item-span {
  color: red;
  font-size: 50px;
  display: block;
}
